.header__button {
  color: #a9a9a9;
  width: 56px;
  height: 22px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  border: none;
  background-color: black;
  cursor: pointer;
}
.header__button:hover {
  opacity: 0.6;
  transition: linear 0.2s;
}
